<template>
    <n-space justify="space-around" align="center" size="large">
        <n-card title="Login" hoverable> 
            <n-space vertical>
                <n-alert type="error" v-if="wrongEmail">
                    {{ error }}
                </n-alert>
                <n-input v-model:value="email" id="email"  type="text" placeholder="Enter username" />
            </n-space>
            <n-divider />
            
            <n-button type="primary" @click="loginUser">Login</n-button>
            <br />
            <n-tooltip :overlap="overlap" placement="bottom" trigger="click">
                <template #trigger>
                    users 
                </template>
                <n-log
                    :rows="10"
                    :log="`
                    Sincere@april.biz
                    Shanna@melissa.tv
                    Nathan@yesenia.net
                    Julianne.OConner@kory.org
                    Lucio_Hettinger@annie.ca
                    Karley_Dach@jasper.info
                    Telly.Hoeger@billy.biz
                    Sherwood@rosamond.me
                    Chaim_McDermott@dana.io
                    Rey.Padberg@karina.biz`"
                    />
            </n-tooltip>
        </n-card>
    </n-space>
</template>
<style scoped>
#email {
    text-align: left !important;
}
</style>
<script>
import { NButton,NSpace,NCard,NDivider,NInput,NAlert,NLog,NTooltip } from 'naive-ui'
export default {
  name: 'Login',
  data() {
        return {
            email : null,
            wrongEmail : false,
            error : null
        }
  },
  beforeCreate: function () {
    if (localStorage.activeUserId!='' && localStorage.activeUserId!=null && localStorage.activeUserId!=undefined) {
      this.$router.push('/dashboard')
    }
  },
  components: {
      NButton,
      NSpace,
      NCard,
      NDivider,
      NInput,
      NAlert,
      NLog,
      NTooltip
  },
  methods: {
      async loginUser() {
          if(this.email==null) {
            this.wrongEmail = true
            this.error = "username cannot be blank" 
          } else {
            var emailAddress = this.email
            var isFound = false
            await fetch('https://jsonplaceholder.typicode.com/users')
                .then((response) => response.json())
                .then((json) => {
                    console.log(json)
                    json.forEach(function(value) {
                        if(value.email==emailAddress) {
                            localStorage.activeUserId = value.id;
                            localStorage.userName = value.name;
                            localStorage.userEmail = value.email;
                            isFound = true;
                            return true;
                        }
                    });
                })
                if(!isFound) {
                    this.wrongEmail = true   
                    this.error = "username doen not exist"  
                } else {
                     this.$router.push('/dashboard')
                }
          }
      }
  }
}
</script>