<template>
  <n-layout has-sider>
    <n-layout-sider bordered content-style="padding: 24px;"
      >
      <n-space vertical>
        <n-avatar
            round
            :size="64"
            src="https://07akioni.oss-cn-beijing.aliyuncs.com/07akioni.jpeg"
        />
        {{ userName }}
        <br>
        {{ emailId }}
        <n-button type="error" @click="logout">logout</n-button>
        </n-space>  
      </n-layout-sider
    >
    <n-layout>
        <n-space align="baseline" justify="content-between" style="margin-left:50px;" vertical>
            <h1>{{ postTitle }}</h1>
            <p>
                {{ postDescription }}
            </p>
            <n-divider />
            <h4>Comments</h4>
            <div v-for="(comment) in comments" :key="comment">
                <div class="light-green" >
                    <n-card hoverable>
                        <p class="bodyData">{{ comment.body }}</p>
                        <template #footer>By : {{comment.email}} </template>
                    </n-card>
                </div>
            </div>
        </n-space>
    </n-layout>
  </n-layout>
</template>

<style>
.light-green {
  /* height: 108px; */
  margin: 10px;
  padding : 5px;
  /* background-color: rgba(0, 128, 0, 0.12); */
}
.green {
  height: 108px;
  background-color: rgba(0, 128, 0, 0.24);
}
.n-card__footer{
    text-align: right;
}

</style>
<script>
import { NLayout,NLayoutSider,NSpace,NAvatar,NButton,NCard,NDivider } from 'naive-ui'
export default {
    name: 'Login',
    components: {
      NLayout,NLayoutSider,NSpace,NAvatar,NButton,NCard,NDivider
    },
    props : ['id'],
    data() {
        return {
            emailId : null,
            username : false,
            error : null,
            userName: null,
            userId : null,
            posts : [],
            postTitle:null,
            postDescription:null,
            comments : []
        }
    },
    mounted() {
        this.userName = localStorage.userName;
        this.emailId = localStorage.userEmail;
        this.userId = localStorage.activeUserId;

        this.fetchPostData()
        this.fetchPostComments()
    },
    methods :{
        logout() {
            localStorage.activeUserId = '';
            localStorage.userName = '';
            localStorage.userEmail = '';
            this.$router.push('/')
        },
        fetchPostData() {
            fetch('https://jsonplaceholder.typicode.com/posts/'+this.id)
            .then((response) => response.json())
            .then((json) => {
                this.postTitle = json.title
                this.postDescription = json.body
            });
        },
        fetchPostComments() {
            fetch('https://jsonplaceholder.typicode.com/posts/'+this.id+'/comments')
            .then((response) => response.json())
            .then((json) => {
                console.log(json)
                this.comments = json
            });
        },
  }
}
</script>
