<template>
  <n-layout has-sider>
    <n-layout-sider bordered content-style="padding: 24px;"
      >
      <n-space vertical>
        <n-avatar
            round
            :size="64"
            src="https://07akioni.oss-cn-beijing.aliyuncs.com/07akioni.jpeg"
        />
        {{ userName }}
        <br>
        {{ emailId }}
        <n-button type="error" @click="logout">logout</n-button>
        </n-space>  
      </n-layout-sider
    >
    <n-layout>
        <n-space align="baseline" vertical>
            <n-space justify="space-between" vertical>
                <h1 style="margin-left:20px;">All Posts</h1>
                <n-button type="info" @click="createPost" style="">Create Post</n-button>
            </n-space>
            <n-space justify="content-between">
                <div v-for="(post) in posts" :key="post">
                    <div class="light-green" >
                        <n-card hoverable :title="post.title.substring(1, 20)">
                            <p class="bodyData">{{ post.body.substring(1, 50) }}</p> 
                            <template #action> 
                                <n-button type="success" @click="viewPost(post.id)">
                                    View
                                </n-button>
                            </template>
                        </n-card>
                    </div>
                </div>
            </n-space>
        </n-space>
    </n-layout>
  </n-layout>
</template>

<style>
.light-green {
  /* height: 108px; */
  margin: 15px;
  padding : 5px;
  /* background-color: rgba(0, 128, 0, 0.12); */
}
.green {
  height: 108px;
  background-color: rgba(0, 128, 0, 0.24);
}

</style>
<script>
import { NLayout,NLayoutSider,NSpace,NAvatar,NButton,NCard } from 'naive-ui'
export default {
    name: 'Login',
    components: {
      NLayout,NLayoutSider,NSpace,NAvatar,NButton,NCard
    },
    data() {
        return {
            emailId : null,
            username : false,
            error : null,
            userName: null,
            userId : null,
            posts : []
        }
    },
    mounted() {
        this.userName = localStorage.userName;
        this.emailId = localStorage.userEmail;
        this.userId = localStorage.activeUserId;
        console.log(this.userInfo)
        this.fetchData()
    },
    methods :{
        logout() {
            localStorage.activeUserId = '';
            localStorage.userName = '';
            localStorage.userEmail = '';
            this.$router.push('/')
        },
        fetchData() {
            fetch('https://jsonplaceholder.typicode.com/posts?userId='+this.userId)
            .then((response) => response.json())
            .then((json) => {
                this.posts = json
            });
        },
        viewPost(id) {
            this.$router.push('/view/'+id)
        },
        createPost() {
            this.$router.push('/create')
        }
  }
}
</script>
