<template>
    
    <n-layout has-sider>
        <n-layout-sider bordered content-style="padding: 24px;">
            <n-space vertical>
                <n-avatar
                    round
                    :size="64"
                    src="https://07akioni.oss-cn-beijing.aliyuncs.com/07akioni.jpeg"
                />
                {{ userName }}
                <br>
                {{ emailId }}
                <n-button type="error" @click="logout">logout</n-button>
            </n-space>  
        </n-layout-sider>
    <n-layout>
        <n-space align="baseline" vertical>
            <h1 style="margin-left:20px;">Create New Posts</h1>
            <n-space justify="center" style="margin-left:50px;">
                <n-space align="baseline" vertical>
                    <n-alert type="error" v-if="error!=null">
                        {{ error }}
                    </n-alert>
                    <n-alert type="success" v-if="insertSuccess">
                        Post Added Successfully
                    </n-alert>
                    <n-form-item label="Title">
                        <n-input placeholder="Title" id="inputData" v-model:value="title" />
                    </n-form-item>
                    <n-form-item label="Description">
                        <n-input placeholder="Description" id="inputData" v-model:value="description"  type="textarea"
                            :autosize="{
                                minRows: 10,
                                maxRows: 20,
                                minCols: 10,
                            }" />
                    </n-form-item>
                    <n-button type="success" @click="savePost">
                        Save
                    </n-button>
                </n-space>
            </n-space>
        </n-space>
    </n-layout>
  </n-layout>
</template>
<style>
#inputData {
    text-align: left !important;
    width: 250px !important;
}
</style>
<script>
import { NLayout,NLayoutSider,NSpace,NAvatar,NButton,NFormItem,NInput,NAlert } from 'naive-ui'
export default {
    components : {
        NLayout,NLayoutSider,NSpace,NAvatar,NButton,NFormItem,NInput,NAlert
    },
    data() {
        return {
            emailId : null,
            username : null,
            error : null,
            userName: null,
            userId : null,
            title : null,
            description : null,
            insertSuccess : false,
        }
    },
    mounted() {
        this.userName = localStorage.userName;
        this.emailId = localStorage.userEmail;
        this.userId = localStorage.activeUserId;
    },
   methods: {
       savePost() {
           if(this.title==null || this.description==null) {
               this.error = "Title or Description Cannot be null"
           } else {
               this.error = null
                fetch('https://jsonplaceholder.typicode.com/posts', {
                    method: 'POST',
                    body: JSON.stringify({
                        title: this.title,
                        body: this.description,
                        userId: this.userId,
                    }),
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                })
                .then((response) => response.json())
                .then((json) => {
                    if(json.id!=undefined) {
                        this.insertSuccess = true
                        this.movetoDashboard()
                    }
                });
           }
        },
        movetoDashboard() {
            setTimeout(() => {this.$router.push('/dashboard')}, 2000);
        }
   }
}
</script>
